body {
    padding: 0;
    margin: 0;
    overflow: hidden;
  }

canvas {
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  z-index: -10;
}

#qr-canvas{
  padding: 0;
  margin: auto;
  display: block;
  width: 800px;
  height: 600px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  opacity: 0.75;
}

#p5js-container {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  width: 640px;
  height: 480px;
  z-index: 10;
  opacity: 0.85;
}

#hydra-container {
  width: 100vw;
  height: 100vh;
}

#hydra-canvas {
  width: 100%;
  height: 100%;
}

